<template>
    <div>
      <div class="row">
        <div class="col-md-11 col-sm-6">
          <h3 class="fw-bold">Add Sub-admin</h3>
        </div>
        <div class="col-md-1 col-sm-6 my-2">
          <router-link to="/admin/subadmins">
            <v-btn color="error">
              <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>List</v-btn>
          </router-link>
        </div>
      </div>

      <template>
        <div class="card">
          <div class="card-body my-4">
            <v-form ref="form" v-model="valid" lazy-validation>
              <v-text-field 
                  v-model="formData.name"
                  :rules="nameRules" label="Name"></v-text-field>
              <v-text-field
                  v-model="formData.username"
                  :rules="usernameRules" 
                  @keyup="userNameVerify"
                  :error-messages="userNameVerifyMessage"
                  label="Username" dense></v-text-field>
              <v-text-field 
                  type="email"
                  v-model="formData.email"
                  :rules="emailRules"
                  @keyup="emailVerify"
                  :error-messages="emailVerifyMessage"
                  label="E-mail"></v-text-field>
              <v-text-field 
                  type="number"
                  v-model="formData.mobile_phone"
                  :rules="phoneRules" 
                  @keyup="phoneVerify"
                  :error-messages="phoneVerifyMessage"
                  label="Phone Number" dense></v-text-field>
              <v-text-field v-model="formData.password" :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :rules="[rules.required, rules.min]" :type="show1 ? 'text' : 'password'"
                  name="input-10-1" label="Password" hint="At least 8 characters" counter
                  @click:append="show1 = !show1"></v-text-field>
              <v-autocomplete label="Select City"
                  v-model="formData.city_id"
                  :rules="cityRules" 
                  :items="allCities" 
                  item-value="city_id"
                  item-text="city" 
                  dense></v-autocomplete>
              <v-col cols="12" sm="6" md="12" class="py-0 d-flex" style="justify-content: space-between;">
                  <div></div>
                  <div class="ms-3 pb-3">
                    <v-btn class="my-4 me-3 cred" @click="reset">CANCEL</v-btn>
                    <v-btn v-if="isLoading" class="my-2 me-3 cred">Loading...</v-btn>
                    <v-btn v-else :disabled="!valid" class="my-4 me-3 cred" @click="formSubmit">SAVE</v-btn>
                  </div>
              </v-col>
            </v-form>
          </div>
        </div>
      </template>
      <v-snackbar v-model="snackbar" :timeout="snackbar.timeout" class="snackbar-shadow">{{ text }}
        <template v-slot:action="{ attrs }">
          <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
        </template>
      </v-snackbar>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: "SubAdmins",
    components: {  },
    data: () => ({
        show1: false,
        valid: true,
        isLoading: false,
        snackbar: false,
        text: '',
        timeout: '',
        emailVerifyMessage:'',
        userNameVerifyMessage:'',
        phoneVerifyMessage: '',
        allCities: [],
        formData:{
          name: '',
          email: '',
          username: '',
          password: '',
          mobile_phone: '',
          city_id: '',
          user_role: '3',
          user_status: '1',
        },
        nameRules: [
            v => !!v || 'Name is required',
            // v => (v && v.length <= 10) || 'Name must be less than 10 characters',
        ],
        usernameRules: [
            v => !!v || 'Username is required',
            v => (v.split(' ').length <= 1) || 'space not allowed',
            v => /^[\w\s]+$/.test(v) || 'Username may only contain letters, numbers and underscores'
        ],
        emailRules: [
          v => !!v || 'E-mail is required.',
          v => /.+@.+/.test(v) || 'E-mail must be valid.',
        ],
        rules: { // pass
            required: value => !!value || 'Password is required.',
            min: v => v.length >= 8 || 'Min 8 characters',
        },
        cityRules: [
          v => !!v || 'City is required.',
        ],
        phoneRules: [
          v => !!v || 'Phone number is required.',
          v => v.length == 10 || 'Phone number must be 10 characters.',
        ],
        checkbox: false,
    }),
    created() {
        this.all_Cities();
    },
    methods: {
        reset(){
          this.$refs.form.reset();
        },
        all_Cities() { // get city
          axios.get('allcities')
              .then((resp) => {
                this.allCities = resp.data.locations;
              });
        },
        emailVerify(){ // verify email
          if(/.+@.+/.test(this.formData.email)){
            // console.warn(this.formData.email);
              axios.post('user/verify', {'email':this.formData.email})
                  .then((resp) => {
                    if(resp.data.status==400){
                        this.emailVerifyMessage = resp.data.message;
                    }else{
                        this.emailVerifyMessage = "";
                    }
              });
            }
        },
        phoneVerify(){ // verify phone
          if(this.formData.mobile_phone.length >= 9){
              axios.post('user/verify', {'mobile_phone':this.formData.mobile_phone})
                  .then((resp) => {
                    if(resp.data.status==400){
                        this.phoneVerifyMessage = resp.data.message;
                    }else{
                        this.phoneVerifyMessage = "";
                    }
              });
          }
        },
        userNameVerify(){ // verify username
          if(this.formData.username){
            axios.post('user/verify', {'username':this.formData.username})
                .then((resp) => {
                  if(resp.data.status==400){
                      this.userNameVerifyMessage = resp.data.message;
                  }else{
                      this.userNameVerifyMessage = "";
                  }
            });
          }
        },
        formSubmit() {
            const config = {
                headers: { 'content-type': 'multipart/form-data' }
            }
            let data = { 
                name: this.formData.name,
                email: this.formData.email,
                username: this.formData.username,
                password: this.formData.password,
                mobile_phone: this.formData.mobile_phone,
                city_id: this.formData.city_id,
                user_role: this.formData.user_role,
                user_status: this.formData.user_status,
            };
            if(this.formData.name!='' && this.formData.email!='' && this.formData.username!='' && this.formData.password!='' && this.formData.mobile_phone!='' && this.formData.city_id!='' && this.formData.user_role!=''){
              this.isLoading = true;
              axios.post("admin/add/subadmin", data, config)
                .then((resp) => {
                    this.isLoading = false;
                    this.snackbar = true;
                    this.text = resp.data.message;
                    this.timeout = 3000;
                    this.$refs.form.reset();
                })
            }
            this.$refs.form.validate()
        },
    },
}
</script>